import React, { Component } from "react";
import Header from "./Header";
import "../App.css";

const images = [{id:0, string:"DSC_0398.jpg"},{id:1, string:"DSC_0444.jpg"},{id:2, string:"DSC_0531.jpg"},{id:3, string:"DSC_1039.jpg"},{id:4, string:"DSC_1263.jpg"},{id:5, string:"DSC_1989.jpg"}];



class Photography extends Component {
  render() {
    let imagesArray=images.map((image, i)=>{
      return(
       <div className="col-lg-3 col-md-4 col-sm-6 col-xs-12" key={i}>
        <img
          src={require(`../img/${image.string}`)}
          className="photoGallery"
        />
        </div>
      )
    })


    return (
      <div className="App container ">
        <Header />
        PHOTOGRAPHY
        <div className="row">
          {imagesArray}
        </div>


      </div>
    );
  }
}


export default Photography;



